import React, { useEffect, useState } from 'react';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Grid, Container } from '@mui/material';
import { Route, Routes, BrowserRouter, useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';

import Sidebar from './components/Sidebar';
import About from './routes/About';
import Work from './routes/Work';
import Contact from './routes/Contact';
import Experience from './routes/Experience';
import './App.css';

function App() {
  const [activeTab, setActiveTab] = useState('/');

  const handleTabChange = (event, newValue) => {
    /*setActiveTab(newValue);*/
  };

  const darkTheme = createTheme({
    palette: {
      mode: 'dark'
    }
  });

  /*function Tabs() {
    return(
      <div>
        {activeTab === 0 && <About theme={darkTheme} />}
        {activeTab === 1 && <Work theme={darkTheme} />}
        {activeTab === 2 && <Experience theme={darkTheme} />}
        {activeTab === 3 && <Contact theme={darkTheme} />}
      </div>
    );
  }*/

  function LocationIdentifier() {
    const { pathname } = useLocation();

    useEffect(() => {
      setActiveTab( pathname );
    }, [pathname]);

    return null;
  }

  function GetMeta() {
    const { pathname } = useLocation();
    const meta = {
      '/': [ 'Rondeo Balos - Creative Web Solutions', 'I\'m an adaptable individual with a passion for success. Seeking a job with a reputable company to expand my knowledge and skills, and make a positive impact.' ],
      '/work/': [ 'Projects | Rondeo Balos - Creative Web Solutions', 'Seeking an opportunity to use my knowledge and experience to help people. By the way, check out my awesome work.' ],
      '/experience/': [ 'Experiences | Rondeo Balos - Creative Web Solutions', 'My work experiences have equipped me with the skills, knowledge, and work ethic necessary to excel in any role within the industry.' ],
      '/contact/': [ 'Contact | Rondeo Balos - Creative Web Solutions', 'Get in touch today! Whether you have a question about my services, want to collaborate on a project, or just want to say hello, I\'d love to hear from you.' ]
    };
    return(
      <Helmet>
        <title>{meta[pathname][0]}</title>
        <meta name='description' content={meta[pathname][1]} />
      </Helmet>
    );
  }

  return (
    <BrowserRouter>
    <LocationIdentifier />
    <ThemeProvider theme={darkTheme}>
      <div style={{ 
        backgroundColor: `${darkTheme.palette.background.default+'44'}`, 
        minHeight: '100vh',
        display: 'flex', 
        alignItems: 'center', 
        justifyContent: 'center'
      }} >
        <div id='bg'>
          <div id='bg1'></div>
          <div id='bg2'></div>
          <div id='bg3'></div>
        </div>
        <GetMeta />
        <Container className='main-content' style={{ display: 'flex', transition: '.3s ease' }}>
          <Grid container spacing={2} style={{ flex: 1 }}>
            <Grid item xs={12} md={3}>
              <Sidebar theme={darkTheme} activeTab={activeTab} handleTabChange={handleTabChange}/>
            </Grid>
            <Grid item xs={12} md={9}>
              <div style={{ padding: 16 }}>
                <Routes>
                  <Route path='/' element={<About/>} />
                  <Route path='/work/' element={<Work/>} />
                  <Route path='/experience/' element={<Experience/>} />
                  <Route path='/contact/' element={<Contact/>} />
                </Routes>
              </div>
            </Grid>
          </Grid>
        </Container>
      </div>
    </ThemeProvider>
    </BrowserRouter>
    
  );
}

export default App;
