import React, { useEffect, useRef, useState } from 'react';
import { Typography, TextField, Button, Box, IconButton, Stack, Backdrop, CircularProgress, Modal, Paper } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import DeleteIcon from '@mui/icons-material/Delete';

import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import LinkIcon from '@mui/icons-material/Link';
import GitHubIcon from '@mui/icons-material/GitHub';

import { useForm, ValidationError } from '@formspree/react';
import { red } from '@mui/material/colors';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    boxShadow: 24,
    borderRadius: '7px',
    p: 10
};

function Form() {
    const [state, handleSubmit] = useForm( 'mpzevyar' );
    const [open, setOpen] = useState( false );
    const [modalOpen, setModalOpen] = useState( false );

    const formRef = useRef( null );
    const handleReset = (event) => {
        if( formRef.current ) {
            formRef.current.reset();
        }
    }

    const submit = (event) => {
        setOpen( true );
        handleSubmit( event );
    };

    useEffect( () => {
        setOpen( false );
        if( state.succeeded ) {
            setModalOpen( true );
            if( formRef.current ) {
                formRef.current.reset();
            }
        }
    }, [state.succeeded] );

    useEffect( () => {
        setOpen( false );
    }, [state.errors] );

    const handleClose = () => setModalOpen( false );

    return(
        <div>
            <form onSubmit={submit} ref={formRef}>
                <Box className={'textinput'}>
                    <TextField label='Name' id='name' name='name'  variant='outlined' margin='normal'fullWidth required/>
                    <ValidationError style={{ color: red }} prefix='Name' field='name' errors={state.errors} />
                </Box>
                <Box className={'textinput'}>
                    <TextField label='Email' id='email' name='email' variant='outlined' margin='normal' type={'email'} fullWidth required/>
                    <ValidationError prefix='Email' field='email' errors={state.errors} />
                </Box>
                <Box className={'textinput'}>
                    <TextField label='Subject' id='subject' name='subject' variant='outlined' margin='normal' fullWidth required/>
                    <ValidationError prefix='Subject' field='subject' errors={state.errors} />
                </Box>
                <Box className={'textinput'}>
                    <TextField label='Message' id='message' name='message' variant='outlined' margin='normal' multiline minRows={3} fullWidth required/>
                    <ValidationError prefix='Message' field='message' errors={state.errors} />
                </Box>
                <div style={{ marginTop: 16, marginRight: 16, display: 'inline-block' }} >
                    <Button variant='contained' size='large' margin='normal' endIcon={<SendIcon />} disabled={state.submitting} type='submit'>Send Message</Button>
                </div>
                <div style={{ marginTop: 16, display: 'inline-block' }} >
                    <Button variant='outlined' size='large' margin='normal' endIcon={<DeleteIcon />} disabled={state.submitting} onClick={handleReset}>Reset</Button>
                </div>
            </form>
            <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={open} >
                <CircularProgress color="inherit" />
            </Backdrop>
            <Modal open={modalOpen} onClose={handleClose} aria-labelledby="modal-modal-title" aria-describedby="modal-modal-description" >
                <Paper sx={style}>
                    <Typography variant='body2' id="modal-modal-description" color='text.primary' style={{ fontSize: '1.5rem', lineHeight: '1.65', fontWeight: 400, textAlign: 'center' }}>
                        Thanks for contacting!
                    </Typography>
                </Paper>
                
            </Modal>
        </div>
        
    );
}

function Contact( {theme} ) {

    return (
        <div>
            <Typography variant='h4' component='h1' className='heading' color='text.primary'>
                <span>Contact</span>
            </Typography>
            <Form />
            <div style={{ height: 50 }}/>
            <Stack direction='row' alignItems='center' flexWrap='wrap' gap='20px' >
                <IconButton aria-label='whatsapp' href='https://wa.me/639615936086' rel='nofollow' target='_blank'
                    sx={{
                        width: 48,
                        height: 48,
                        border: '2px solid',
                        borderRadius: '50%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                      }}
                >
                    <WhatsAppIcon />
                </IconButton>
                <IconButton aria-label='linkedin' href='https://www.linkedin.com/in/rondeo-balos/' rel='nofollow' target='_blank'
                    sx={{
                        width: 48,
                        height: 48,
                        border: '2px solid',
                        borderRadius: '50%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                      }}
                >
                    <LinkedInIcon />
                </IconButton>
                <IconButton aria-label='itch.io' href='https://rondeo.itch.io/' rel='nofollow' target='_blank'
                    sx={{
                        width: 48,
                        height: 48,
                        border: '2px solid',
                        borderRadius: '50%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                      }}
                >
                    <LinkIcon />
                </IconButton>
                <IconButton aria-label='github' href='https://github.com/rondeo-balos' rel='nofollow' target='_blank'
                    sx={{
                        width: 48,
                        height: 48,
                        border: '2px solid',
                        borderRadius: '50%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                      }}
                >
                    <GitHubIcon />
                </IconButton>
                <IconButton aria-label='playstore' href='https://play.google.com/store/apps/dev?id=8521617602390433142' rel='nofollow' target='_blank'
                    sx={{
                        width: 48,
                        height: 48,
                        border: '2px solid',
                        borderRadius: '50%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                      }}
                >
                    <LinkIcon />
                </IconButton>
            </Stack>
        </div>
    );
}

export default Contact;