import React, { useEffect, useState } from 'react';
import { Typography, Container, Paper, Tabs, Tab, Grid, IconButton, useMediaQuery, styled } from '@mui/material';
import { animated, useSpring } from 'react-spring';

import Logo from '../logo.png';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { Link } from 'react-router-dom';

function Sidebar({ theme, activeTab, handleTabChange }) {
    const isMobile = useMediaQuery( theme.breakpoints.down( 'md' ) );
    const [expand, setExpand] = useState( true );

    const rotateStyle = useSpring({
        transform: expand ? 'rotate(360deg)' : 'rotate(0deg)',
        display: 'flex', 
        alignItems: 'center', 
        width: 24, 
        height: 24
    });

    const toggleStyle = useSpring({
        maxHeight: expand ? '300px' : '0px',
        height: 'auto',
        overflow: 'hidden',
        transition: '.1s',
        transitionTimingFunction: 'linear'
    });

    function toggleDropdown() {
        if( !isMobile )
            return;
        setExpand((prevExpand) => !prevExpand);
    }

    useEffect(() => {
        if( !isMobile && !expand ) {
            //$( '.dropdown-mobile' ).slideToggle();
            setExpand( !expand );
        }
    }, [isMobile, expand]);

    const StyledContainer = styled(Container)( ({theme}) => ({
        [theme.breakpoints.down( 'md' )]: {
            width: 50,
            height: 50,
            marginBottom: 0,
            padding: 5
        },
        [theme.breakpoints.up( 'md' )]: {
            width: 120,
            height: 120,
            marginBottom: 20,
            padding: 24
        },
        borderRadius: 100,
        border: 'solid 1px #fff'
    }) );
    
    const StyledPaper = styled(Paper)( ({theme}) => ({
        borderRadius: 20, 
        padding: 16, 
        margin: 10, 
        boxSizing: 'border-box', 
        marginBottom: '10px',
        minHeight: 'calc(100% - 20px)',
        [theme.breakpoints.down( 'md' )]: {
            minHeight: 'auto'
        }
    }) );

    const StyledGrid = styled(Grid)( 
        ({theme}) => ({
            [theme.breakpoints.up( 'md' )]: {
                display: 'none'
            }
        })
     );
    
    return (
        <StyledPaper>
            <Grid container alignItems="center">
                {/* Logo */}
                <Grid item xs={3} sm={3} md={3} lg={12}>
                    <StyledContainer>
                        <img src={Logo} alt='Logo' style={{ objectFit: 'contain', width: '100%', height: '100%' }} />
                    </StyledContainer>
                </Grid>

                {/* Title */}
                <Grid item xs={6} sm={6} md={6} lg={12} style={{ textAlign: 'center' }}>
                    <Typography component='span' variant='h5' style={{ marginBottom: 16 }} sx={{ fontSize: isMobile ? '1rem' : '1.5rem' }}>
                        Rondeo Balos
                    </Typography>
                </Grid>

                {/* Dropdown Button */}
                <StyledGrid item xs={3} sm={3} md={3} lg={12} style={{ textAlign: 'right' }}>
                    <IconButton onClick={toggleDropdown}>
                        <animated.span style={rotateStyle}>
                            { expand ? <CloseIcon/> : <MenuIcon /> }
                        </animated.span>
                    </IconButton>
                </StyledGrid>
                
            </Grid>

            {/*<Divider style={{ marginBottom: 20 }} />*/}

            {/* Add your sidebar content here */}
            <animated.div style={toggleStyle}>
                <div style={{ height: 20, borderBottom: 'solid 1px rgba(255, 255, 255, 0.12)', marginBottom: 10, marginLeft: -16, marginRight: -16 }} />
                <Tabs value={activeTab} onChange={handleTabChange} orientation='vertical' variant='scrollable' scrollButtons='auto' indicatorColor='primary' >
                    <Tab label='About' value='/' onClick={toggleDropdown} component={Link} to='/' />
                    <Tab label='Work' value='/work/' onClick={toggleDropdown} component={Link} to='/work/' />
                    <Tab label='Experience' value='/experience/' onClick={toggleDropdown} component={Link} to='/experience/' />
                    <Tab label='Contact' value='/contact/' onClick={toggleDropdown} component={Link} to='/contact/' />
                </Tabs>
            </animated.div>
        </StyledPaper>
    );
}

export default Sidebar;
